<template lang="pug">
  Modals(
    :active="isNotice"
    :isCanClose="false"
    contentTextAlign="left"
    @close="onClose"
  )
    template(slot="header") 
    template(slot="body")
      |1.48 
      br
      |2.APPhttps://www.einvoice.nat.gov.tw/
    template(slot="footer")
      button(
        class="btn btn-submit btn-block btn-sm"
        @click="onClose"
      ) 
</template>

<script>
import Modals from '@/components/units/Modals';

export default {
  name: 'noticeModal',
  props: {
    isNotice: {
      type: Boolean,
      required: true,
      default: false,
    },
    onClose: {
      type: Function,
      required: true,
    },
  },
  components: {
    Modals,
  },
};
</script>



